import React, { Component, useState } from 'react';

export default class Toolbar extends Component {
    render() {
        return (
            <div className='time-format-section'>
                <a><i class="bi bi-file-earmark-pdf-fill ps-3" data-bs-toggle="tooltip" data-bs-placement="top" title="Exporter vers pdf"></i></a>
                <a><i class="bi bi-printer-fill ps-3" data-bs-toggle="tooltip" data-bs-placement="top" title="Imprimer"></i></a>
                <i class="bi bi-file-earmark-plus-fill ps-3" data-bs-toggle="tooltip" data-bs-placement="top" title="Annuler"></i>
               </div>
        );
    }
}