import React, { useState } from 'react';
import { BrowserRouter, Routes, Route, Link } from "react-router-dom";


import './Dash.css';

import Tableaub from '../TableauBord/Tableaub';
import Reservation from '../Reservation/Reservation';
import Terrain from '../Terrain/Terrain';
import Associations from '../Associations/Associations';
import User from '../User/User';
import Supliments from '../Supliments/Supliments';
import Planing from '../Planing/Planing';
export default function Dashboard() {
  const [isactive, setActive] = useState(false);
  const toggleClass = () => {
    setActive(!isactive);
  };
  return(
<>
<BrowserRouter basename={process.env.PUBLIC_URL}>
      <div className={isactive ? 'vertical-nav bg-white active' :'vertical-nav bg-white'}  id="sidebar">
        <div class="py-4 px-3 mb-4 bg-light">
          <div class="media d-flex align-items-center">
              <img src="https://bootstrapious.com/i/snippets/sn-v-nav/avatar.png" alt="..." width="65" class="mr-3 rounded-circle img-thumbnail shadow-sm"/>
            <div class="media-body">
              <h4 class="m-0">Arouss abdel</h4>
              <p class="font-weight-light text-muted mb-0">Fonction</p>
            </div>
          </div>
          <Link to="/"><i class="bi bi-house-door-fill pe-2"></i></Link>
          <Link to=""><i class="bi bi-wrench-adjustable pe-2"></i></Link>
          <Link to=""><i class="bi bi-plug-fill pe-2"></i></Link>
          
          
          
        </div>

        <p class="text-gray font-weight-bold text-uppercase px-3 small pb-2 mb-0 bg-light">
        <i class="bi bi-alarm"></i>
          Rservation</p>

        <ul class="nav flex-column bg-white mb-0">
          <li class="nav-item">
                  <Link to="/Reservation"  class="nav-link text-dark font-italic">
                      Nouveau Rservation
                  </Link>
          </li>
        <li class="nav-item">
                  <Link to="/ModReservation"  class="nav-link text-dark font-italic">
                      Modifier Rservation
                  </Link>
          </li>
          <li class="nav-item">
                  <Link to="/SupReservation"  class="nav-link text-dark font-italict">
                      Supprimer Rservation
                  </Link>
          </li>
        </ul>


        <p class="text-gray font-weight-bold text-uppercase px-3 small py-2 mb-0 bg-light">
        <i class="bi bi-cash-coin"></i>
          Comptabilit</p>

        <ul class="nav flex-column bg-white mb-0">
          <li class="nav-item">
            <a href="#" class="nav-link text-dark font-italic">
              Les entres
                  </a>
          </li>
          <li class="nav-item">
            <a href="#" class="nav-link text-dark font-italic">
              Les sorties
                  </a>
          </li>
          <li class="nav-item">
            <a href="#" class="nav-link text-dark font-italic">
              Reporting
                  </a>
          </li>
        </ul>

        <p class="text-gray font-weight-bold text-uppercase px-3 small py-2 mb-0 bg-light">
        <i class="bi bi-tools"></i>
          Administration</p>

      <ul class="nav flex-column bg-white mb-0">
      <li class="nav-item">
          <Link to="/Terrain" class="nav-link text-dark font-italic">
                    Terrain
                </Link>
        </li>
        <li class="nav-item">
          <Link to="/Associations" class="nav-link text-dark font-italic">
         Associations
                </Link>
        </li>
        <li class="nav-item">
          <Link to="/Supliments" class="nav-link text-dark font-italic">
          Supliments
                </Link>
        </li>
        <li class="nav-item">
        <Link to="/User" class="nav-link text-dark font-italic">
                    Utilisateurs
                </Link>
        </li>
        <li class="nav-item">
          <Link to="/Planing"  class="nav-link text-dark font-italic bg-light">
                    Planing
                </Link>
        </li>
      </ul>
      </div>
      
      <div className={isactive ? 'page-content active p-2' :'page-content p-2'} id="content">
          <button id="sidebarCollapse" type="button"
          class="btn btn-light bg-white rounded-pill shadow-sm" onClick={toggleClass}>
            {
              isactive ? <i class="bi bi-text-indent-left"></i> : <i class="bi bi-text-indent-right"></i>
            }
          </button>
          <Routes>
          <Route path="/" element={<Tableaub />} />
            <Route path="/Reservation" element={<Reservation />} />
            <Route path="/Terrain" element={<Terrain />} />
            <Route path="/Associations" element={<Associations />} />
            <Route path="/User" element={<User />} />
            <Route path="/Supliments" element={<Supliments />} />
            <Route path="/Planing" element={<Planing />} />

          </Routes>
      </div>
</BrowserRouter>
</>
    
  );
}