import React, { Component } from 'react';
import Scheduler from './Scheduler/Scheduler';
import Toolbar from './Scheduler/Toolbar';
import MessageArea from './Scheduler/MessageArea';
import './Scheduler/Scheduler.css';
import './Scheduler/Toolbar.css';

 
const data = [
    { start_date:'2022-08-08 6:00', end_date:'2022-08-08 7:00', text:'arouss abdeljalil', id: 1 },
    { start_date:'2022-08-08 6:00', end_date:'2022-08-08 7:00', text:'Event 1', id: 2 },
    { start_date:'2022-08-08 7:00', end_date:'2022-08-08 8:00', text:'arouss abdeljalil', id: 3 },
    { start_date:'2022-08-08 7:00', end_date:'2022-08-08 8:00', text:'Event 1', id: 4 },
    { start_date:'2022-08-09 6:00', end_date:'2022-08-09 7:00', text:'arouss abdeljalil', id: 5 },
    { start_date:'2022-08-09 6:00', end_date:'2022-08-09 7:00', text:'Event 1', id: 6 },
    { start_date:'2022-08-10 6:00', end_date:'2022-08-10 7:00', text:'arouss abdeljalil', id: 7 },
    { start_date:'2022-08-11 11:00', end_date:'2022-08-11 12:00', text:'Event 1', id: 8 },

];
 
class Reservation extends Component {
   state = {
    currentTimeFormatState: true,
    messages: []
};
addMessage(message) {
    const maxLogLength = 5;
    const newMessage = { message };
    const messages = [
        newMessage,
        ...this.state.messages
    ];

    if (messages.length > maxLogLength) {
        messages.length = maxLogLength;
    }
    this.setState({ messages });
}

logDataUpdate = (action, ev, id) => {
    const text = ev && ev.text ? ` (${ev.text})` : '';
    const message = `event ${action}: ${id} ${text}`;
    this.addMessage(message);
}
 
    handleTimeFormatStateChange = (state) => {
        this.setState({
            currentTimeFormatState: state
        });
    }
    render() {
        return (
            <div>
              <div className="tool-bar">
                    <Toolbar
                        timeFormatState={this.currentTimeFormatState}
                        onTimeFormatStateChange={this.handleTimeFormatStateChange}
                    />
                </div>
                <div className='scheduler-container'>
                    <Scheduler events={data}/>
                </div>
            </div>
        );
    }
 }
 export default Reservation;