import React, { Component} from 'react';
import jsPDF from 'jspdf'
export default class Toolbar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      associ:true
    };
  }
  handleChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  }
    handleTimeFormatStateChange = (e) => {
        if (this.props.onTimeFormatStateChange) {
            this.props.onTimeFormatStateChange(e.target.checked)
        }
    }
    generatePDF = () => {
      var doc = new jsPDF('p', 'mm', 'a7');
      doc.setFontSize(20);
      doc.text(2, 10, 'ASSOCIATION NAME')
     
      
      doc.setFontSize(10);
      doc.rect(2, 20, 3, 3, 'F')
      doc.text(8, 23, 'Date de matche :')
      doc.text(2, 28, 'Le 14/03/2022 De 06H  07H AM ==>50DH') 

      doc.rect(2, 33, 3, 3, 'F')
      doc.text(8, 36, 'Suppliments :') 
      doc.text(8, 41, 'SUP1===>44DH') 
      doc.text(8, 46, 'SUP2===>44DH') 
      doc.text(8, 51, 'SUP3===>44DH') 
      doc.text(8, 56, 'SUP4===>44DH') 

      doc.setFontSize(15);
      doc.text(2, 61, 'TOTAL : 64DH')
      doc.setFontSize(8);
      doc.text(2, 95, 'CODE_R:RICAR00198')
      doc.text(2, 100, 'CODE_R:DP23DQS923423SDFJ1239SD0')


      doc.setProperties({
        title: `APP RESERVATION ${ Date()}`,
        subject: 'APPLICATION DE RESERVATION',
        author: 'AROUSS ABDELJALIL',
        keywords: 'REACT',
        creator: 'MEEE'
      });
      doc.save('Reservation.pdf')
    }   
    render() {
        return (
            <div className='time-format-section'>
                <a><i class="bi bi-file-earmark-pdf-fill ps-3" data-bs-toggle="tooltip" data-bs-placement="top" title="Exporter vers pdf"></i></a>
                <a><i class="bi bi-printer-fill ps-3" data-bs-toggle="tooltip" data-bs-placement="top" title="Imprimer"></i></a>
                <i class="bi bi-x-diamond-fill  ps-3" data-bs-toggle="tooltip" data-bs-placement="top" title="Annuler"></i>
                <a>
                  <i class="bi bi-file-earmark-plus-fill ps-3" data-bs-toggle="modal" data-bs-target="#exampleModal" data-bs-placement="top" title="Nouveau"></i>
                  </a>        
<div class="modal fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div class="modal-dialog">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title badge bg-primary" id="exampleModalLabel">Nouvelle rservation :</h5>
        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
      </div>
      <div class="modal-body">
      <div class="mb-3">
         <input class="form-control form-control-sm" type="date" aria-label=".form-control-sm example"/>    
        </div>
        <div class="mb-3">
         <input class="form-control form-control-sm" type="time" min="00:00" max="23:00" />
         </div>
         <div class="mb-3">
         <input class="form-control form-control-sm" type="time" min="00:00" max="23:00" />
         </div>
         <div class="mb-3">
         <div class="form-check form-switch">
          <input class="form-check-input" type="checkbox" id="flexSwitchCheckDefault" 
          name="associ" checked={this.state.associ}
          onChange={(e) => {
            this.handleChange({
              target: {
                name: e.target.name,
                value: e.target.checked,
              },
            });
          }}
          />
          <label class="form-check-label badge bg-success" for="flexSwitchCheckDefault">Association Oui/Non</label>
        </div>
        </div> 
        <div class="mb-3">
          {
            this.state.associ ? 
            <select class="form-select form-select-sm" aria-label=".form-select-sm example">
              <option>Association 1</option>
              <option>Association 2</option>
              <option>Association 3</option>
              <option>Association 4</option>
              <option>Association 5</option>
            </select>
             :
            <input class="form-control form-control-sm" type="text" placeholder="Responsable rservation" aria-label=".form-control-sm example"/>
          }
         
        </div>
        <div class="mb-3">
        <select class="form-select form-select-sm" aria-label=".form-select-sm example">
          <option >Terrain 1</option>
          <option >Terrain 2</option>
        </select>
        </div>
 



         <div class="mb-3">
         <p class="badge bg-success">Suppliments :</p>
<ul class="form-control text-start">
  <li class="list-group-item">
    <div class="custom-control custom-checkbox">
      <input type="checkbox" class="custom-control-input" id="check1" />
      <label class="custom-control-label" for="check1">Camera</label>
    </div>
  </li>
  <li class="list-group-item">
    <div class="custom-control custom-checkbox">
      <input type="checkbox" class="custom-control-input" id="check2"/>
      <label class="custom-control-label" for="check2">Ballon</label>
    </div>
  </li>
  <li class="list-group-item">
    <div class="custom-control custom-checkbox">
      <input type="checkbox" class="custom-control-input" id="check3" />
      <label class="custom-control-label" for="check3">Gilet</label>
    </div>
  </li>
  <li class="list-group-item">
    <div class="custom-control custom-checkbox">
      <input type="checkbox" class="custom-control-input" id="check4" />
      <label class="custom-control-label" for="check4">bouteille d'eau</label>
    </div>
  </li>
</ul>
        </div>

        <div class="mb-3">
         <p class="badge bg-success">Rservation valable :</p>
            <select class="form-select form-select-sm" aria-label=".form-select-sm example">
            <option selected>Une Seule fois</option>
            <option >deux semaines</option>
            <option >Un mois</option>
            <option >Trois mois</option>
            <option >six mois</option>
            <option >un an</option>
            </select>
        </div>

        <div class="mb-3"><textarea class="form-control" placeholder="Commentaire" id="floatingTextarea"></textarea>
        </div>
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Ferm</button>
        <button type="button" class="btn btn-primary" onClick={this.generatePDF}>Enregistrer</button>
      </div>
    </div>
  </div>
</div>
            </div>
        );
    }
}